@font-face {
  font-family: 'Pacifico-Regular';
  src: url('../src/assets/fonts/Pacifico-Regular.ttf');
}

.logo {
  font-family: 'Pacifico-Regular', Arial, sans-serif;
  margin-left: 50px;
}

.btn {
  background-color: #d50000 ;
}

.btn:hover {
  background-color: #e57373;
}